import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PatientComponent } from './ReMaxBilling/patient/patient.component';
import { SignInComponent } from './users/sign-in/sign-in.component';
import { AuthGuard } from './services/auth-guard.service';
import { PatientDetailComponent } from './ReMaxBilling/patient/patient-detail.component';
// import { PatientNewDetailComponent } from './ReMaxBilling/patient/patient-detail-new.component';


const routes: Routes = [
  { path: 'signin', component: SignInComponent },
  { path: 'patient', component: PatientComponent, canActivate: [AuthGuard] },
  { path: 'patient', component: PatientComponent, canActivate: [AuthGuard] },
  {
    path: 'patient/:id',
    component: PatientDetailComponent,
    canActivate: [AuthGuard]
  },
  // {
  //   path: 'patientprocredure/:id',
  //   component: PatientNewDetailComponent,
  //   canActivate: [AuthGuard]
  // },
  { path: '', component: SignInComponent },
  { path: '**', component: SignInComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
export const routingComponents = [PatientComponent, PatientDetailComponent
  //  PatientNewDetailComponent
  ];



