import {
  Component,
  OnInit,
  AfterViewInit,
  OnDestroy,
  ViewChild
} from '@angular/core';
import { DataSource } from '@angular/cdk/collections';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import { Http } from '@angular/http';
import { Patient } from '../../view-models/patient.model';
import { of } from 'rxjs/observable/of';
import { FormControl } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { environment } from '../../../environments/environment';
import * as moment from 'moment';
import {
  MatPaginator,
  MatSort, 
  Sort, 
  MatTableDataSource,
  MatDialogRef,
  MatDialog,
  MatCheckbox
} from '@angular/material';
import { filter } from 'rxjs/operators';
import { MatDialogsHelperService } from './mat-dialogs-helper/mat-dialogs-helper.service';
import * as cloneDeep from 'lodash/cloneDeep';

@Component({
  selector: 'app-patient',
  templateUrl: './patient.component.html',
  styleUrls: ['./patient.component.scss']
})
export class PatientComponent implements OnInit, AfterViewInit {
  [x: string]: any;
  
  color: string;
  interval: any;
  patientSc: Observable<any>;
  // patients: Patient[] = [];
  // dataSource: MatTableDataSource<Patient>;
  @ViewChild(MatSort) sort: MatSort;
  dataSource: Patient[] = [];
  sortedData: Patient[] = [];
  myData: any;
  myPreviousData: any;
  displayedColumns = [
    'Reviewed',
    'Service',
    'DateofBirth',
    'MRN',
    'Encounter',
    'CaseId',
    'Name',
    'Location',
    'Procedure',
    'OperativeReport',
    'id'
  ];
  startdate: FormControl;
  enddate: FormControl;
  name: FormControl;
  mrn: FormControl;
  stDate: Date;
  enDate: Date;
  name1: string;
  mrn1: string;
  TotalCount: string;
  SelectedValue: string;
  SelectedValue1: string;
  show: string;
  filter: string;
  value: number;
  encounter: FormControl;
  encounter1: string;

  constructor(
    private http: Http,
    private router: Router,
    private route: ActivatedRoute,
    private dialogs: MatDialogsHelperService
  ) {
    if (this.route.snapshot.queryParamMap.get('startdate') === null) {
      this.stDate = new Date(new Date().setDate(new Date().getDate() - 1));
    } else {
      this.stDate = moment(
        this.route.snapshot.queryParamMap.get('startdate'),
        'YYYY-MM-DD'
      ).toDate();
    }
    if (this.route.snapshot.queryParamMap.get('enddate') === null) {
      this.enDate = new Date(new Date());
    } else {
      this.enDate = moment(
        this.route.snapshot.queryParamMap.get('enddate'),
        'YYYY-MM-DD'
      ).toDate();
    }
    if (
      this.route.snapshot.queryParamMap.get('name') === null ||
      this.route.snapshot.queryParamMap.get('name') === 'null'
    ) {
      this.name1 = '';
    } else {
      this.name1 = this.route.snapshot.queryParamMap.get('name');
    }
    if (
      this.route.snapshot.queryParamMap.get('encounter') === null ||
      this.route.snapshot.queryParamMap.get('encounter') === 'null'
    ) {
      this.encounter1 = '';
    } else {
      this.encounter1 = this.route.snapshot.queryParamMap.get('encounter');
    }

    if (
      this.route.snapshot.queryParamMap.get('mrn') === null ||
      this.route.snapshot.queryParamMap.get('mrn') === 'null'
    ) {
      this.mrn1 = '';
    } else {
      this.mrn1 = this.route.snapshot.queryParamMap.get('mrn');
    }

    this.show = this.route.snapshot.queryParamMap.get('show');
    this.filter = this.route.snapshot.queryParamMap.get('filter');
    this.SelectedValue = this.show;
    this.SelectedValue1 = this.filter;

    this.startdate = new FormControl(this.stDate);
    this.enddate = new FormControl(this.enDate);
    this.name = new FormControl(this.name1);
    this.mrn = new FormControl(this.mrn1);
    this.encounter = new FormControl(this.encounter1);
    this.loadFromFile();
  }

  ngAfterViewInit() {
    this.loadFromFile();
  }
  public openConfirmDialogs(data: Patient) {
    this.dialogs.confirm(data.Reviewed).subscribe(data1 => {
      if (!data1) {
        this.myData = cloneDeep(this.myPreviousData);
        this.onValChange1(this.SelectedValue1);
      } else {
        data.Reviewed = !data.Reviewed;
        this.UpdateStatus(data.ProcId, data.Reviewed);
        this.myPreviousData = cloneDeep(this.myData);
      }
    });
  }

  changeBackground(data): object {
    return { 'background-color': data };
  }

  navigateTo(row: any) {
    let name12: string;
    if (
      this.name.value === '' ||
      this.name.value === ' ' ||
      this.name.value === null ||
      this.name.value === 'null'
    ) {
      name12 = 'null';
    } else {
      name12 = this.name.value;
    }
    let mrn1: string;
    if (
      this.mrn.value === '' ||
      this.mrn.value === ' ' ||
      this.mrn.value === null ||
      this.mrn.value === 'null'
    ) {
      mrn1 = 'null';
    } else {
      mrn1 = this.mrn.value;
    }

    let encounter1: string;
    if (
      this.encounter.value === '' ||
      this.encounter.value === ' ' ||
      this.encounter.value === null ||
      this.encounter.value === 'null'
    ) {
      encounter1 = 'null';
    } else {
      encounter1 = this.encounter.value;
    }

    if(row.Service === 'TA') {
    this.router.navigate(['/patient/' + row.MRN], {
      queryParams: {
        startdate: moment(this.startdate.value).format('YYYY-MM-DD'),
        enddate: moment(this.enddate.value).format('YYYY-MM-DD'),
        name: name12,
        mrn: mrn1,
        encounter: encounter1,
        show: this.SelectedValue,
        filter: this.SelectedValue1
      }
    });
    } else {
    this.router.navigate(['/patientprocredure/' + row.MRN], {
      queryParams: {
        startdate: moment(this.startdate.value).format('YYYY-MM-DD'),
        enddate: moment(this.enddate.value).format('YYYY-MM-DD'),
        name: name12,
        mrn: mrn1,
        encounter: encounter1,
        show: this.SelectedValue,
        filter: this.SelectedValue1
      }
      // console.log('Navigation', this.route.url)
    });
  }
}

  public loadFromFile() {
    let name23 = this.name.value;
    let mrn23 = this.mrn.value;
    let encounter23 = this.encounter.value;
    const onedate = moment(this.startdate.value).format('YYYY-MM-DD');
    const seconddate = moment(this.enddate.value).format('YYYY-MM-DD');
    // tslint:disable-next-line:max-line-length
    if (
      name23 === ' ' ||
      name23 === '' ||
      name23 === 'null' ||
      name23 === null ||
      name23 === 'undefined'
    ) {
      name23 = null;
    }
    if (mrn23 === ' ' || mrn23 === '' || mrn23 === null || mrn23 === 'null') {
      mrn23 = null;
    }
    if (encounter23 === ' ' || encounter23 === '' || encounter23 === null || encounter23 === 'null') {
      encounter23 = null;
    }

    // tslint:disable-next-line:max-line-length
    this.http
      .get(
        environment.api_url +
          '/patient/' +
          'startdate/' +
          onedate +
          '/enddate/' +
          seconddate +
          '/name/' +
          name23 +
          '/mrn/' +
          mrn23 +
          '/encounter/' +
          encounter23
      )
      .subscribe(
        result => {
          this.myData = result.json() as Patient[];

          console.log(this.myData);
          this.myPreviousData = cloneDeep(this.myData);

          if (this.myData.length === 0) {
            this.TotalCount = '';
          } else {
            this.TotalCount = this.myData.length;
          }

          if (this.show === 'All' && this.filter === 'AllCases') {
            this.dataSource = this.myData;
            this.sortedData = this.dataSource.slice();
          } else {
            this.onValChange1(this.SelectedValue1);
          }
        },
        error => console.error(error)
      );
  }

  ngOnInit() {}

  onValChange(value: any) {
    this.SelectedValue = value;
    if (value === 'NotReviewed') {
      if (this.SelectedValue1 === 'TA') {
        this.dataSource = this.myData.filter(
          x => x.Reviewed === false && x.Service === 'TA'
        ) as any;
      } else if (this.SelectedValue1 === 'BP') {
        this.dataSource = this.myData.filter(
          x => x.Reviewed === false && x.Service === 'FF'
        ) as any;
      } else {
        this.dataSource = this.myData.filter(x => !x.Reviewed) as any;
      }
    } else if (value === 'Reviewed') {
      if (this.SelectedValue1 === 'TA') {
        this.dataSource = this.myData.filter(
          x => x.Reviewed && x.Service === 'TA'
        ) as any;
      } else if (this.SelectedValue1 === 'BP') {
        this.dataSource = this.myData.filter(
          x => x.Reviewed && x.Service === 'FF'
        ) as any;
      } else {
        this.dataSource = this.myData.filter(x => x.Reviewed) as any;
      }
    } else {
      this.dataSource = this.myData;
    }
    this.sortedData = this.dataSource.slice();
  }

  onValChange1(value: any) {
    this.SelectedValue1 = value;
    if (value === 'TA') {
      if (this.SelectedValue === 'All') {
        this.dataSource = this.myData.filter(x => x.Service === 'TA') as any;
      } else if (this.SelectedValue === 'Reviewed') {
        this.dataSource = this.myData.filter(
          x => x.Reviewed && x.Service === 'TA'
        ) as any;
      } else {
        this.dataSource = this.myData.filter(
          x => !x.Reviewed && x.Service === 'TA'
        ) as any;
      }
    } else if (value === 'BP') {
      if (this.SelectedValue === 'All') {
        this.dataSource = this.myData.filter(x => x.Service === 'FF') as any;
      } else if (this.SelectedValue === 'Reviewed') {
        this.dataSource = this.myData.filter(
          x => x.Reviewed && x.Service === 'FF'
        ) as any;
      } else {
        this.dataSource = this.myData.filter(x => {
          return !x.Reviewed && x.Service === 'FF';
        }) as any;
      }
    } else {
      if (this.SelectedValue === 'Reviewed') {
        this.dataSource = this.myData.filter(x => x.Reviewed) as any;
      } else if (this.SelectedValue === 'NotReviewed') {
        this.dataSource = this.myData.filter(x => !x.Reviewed) as any;
      } else {
        this.dataSource = this.myData as any;
      }
    }
    this.sortedData = this.dataSource.slice();
  }

  onsubmit() {
    let name = this.name.value;
    let mrn = this.mrn.value;
    let encounter = this.encounter.value;
    const onedate = moment(this.startdate.value).format('YYYY-MM-DD');
    const seconddate = moment(this.enddate.value).format('YYYY-MM-DD');
    if (name === ' ' || name === '') {
      name = null;
    }
    if (mrn === ' ' || mrn === '') {
      mrn = null;
    }
    if (encounter === ' ' || encounter === '') {
      encounter = null;
    }

    this.http
      .get(
        environment.api_url +
          '/patient/' +
          'startdate/' +
          onedate +
          '/enddate/' +
          seconddate +
          '/name/' +
          name +
          '/mrn/' +
          mrn +
          '/encounter/' +
          encounter
      )
      .subscribe(
        result => {
          this.myData = result.json() as Patient[];
          this.myPreviousData = cloneDeep(this.myData);
          if (this.myData.length === 0) {
            this.TotalCount = '';
          } else {
            this.TotalCount = this.myData.length;
          }
          this.SelectedValue = 'All';
          this.SelectedValue1 = 'AllCases';
          this.dataSource = this.myData;
          this.sortedData = this.dataSource.slice();
          // this.dataSource.sort=this.sort;
        },
        error => console.error(error)
      );
  }

  UpdateStatus(procId, status) {

    const url = environment.api_url +
    '/Procedure/UpdateStatus/procId/' +
    procId +
    '/status/' +
    status;
     // tslint:disable-next-line:max-line-length
     this.http
     .post(url, null)
     .subscribe(
       result => {
         this.value = result.json() as number;
       },
       error => console.error(error)
     );
 }

  sortData(sort: Sort) {
    const data = this.dataSource.slice();

    if (!sort.active || sort.direction === '') {

      this.sortedData = data;

      return;

    }

    this.sortedData = data.sort((a, b) => {

      const isAsc = sort.direction === 'asc';

      switch (sort.active) {

        case 'Name': return compare(a.PatientName, b.PatientName, isAsc);

        case 'MRN': return compare(a.MRN, b.MRN, isAsc);

        case 'Encounter': return compare(a.Encounter, b.Encounter, isAsc);

        case 'OperativeReport': return compare(a.OperativeReport, b.OperativeReport, isAsc);

        default: return 0;

      }

    });

  }
}
function compare(a: number | string, b: number | string, isAsc: boolean) {

  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}



