import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Http } from '@angular/http';
import { PatientDetails } from '../../view-models/patient.model';
import { environment } from '../../../environments/environment';
import * as moment from 'moment';
import { IPatientImageDetails } from '../../view-models/patient.model';
import { concat } from 'rxjs/operators';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
//import { IDropdownSettings2 } from 'ng-multiselect-dropdown';

@Component({
  templateUrl: './patient-detail.component.html',
  styleUrls: ['./patient-detail.component.scss'],
  encapsulation: ViewEncapsulation.None,
  selector: 'multiple-demo'
})
export class PatientDetailComponent implements OnInit {
  pageTitle = 'Patient Detail';
  patientdetails: PatientDetails;
  PatientImageDetails: IPatientImageDetails;
  date: string;
  caseId: string;
  OR: string;
  Procedure: string;
  DocumentationInconsistencies: string;
  //emailAlias: []; 

  startdate: string;
  enddate: string;
  mrn: string;
  name: string;
  images = [];
  currentImageTitle: string;
  imageTitles = [];
  myForm: FormGroup;
  disabled = false;
  ShowFilter = false;
  limitSelection = false;
  Inconsistencies = [];
  selectedItems = [];
  dropdownSettings: any = {};
  BillableItems = [];
  selectedBillableItems = [];
  BillableConsiderations = [];
  selectedBillableConsiderations = [];
  dropdownSettings1 = {};
  AnesthesiaTypes = [];
  selectedAnesthesiaType = [];
  dropdownSettings2 = {};
  id = this.route.snapshot.paramMap.get('id');
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private http: Http,
    private fb: FormBuilder
  ) { }

  ngOnInit() {

    this.startdate = this.route.snapshot.queryParamMap.get('startdate');
    this.enddate = this.route.snapshot.queryParamMap.get('enddate');
    this.mrn = this.route.snapshot.queryParamMap.get('mrn');
    this.name = this.route.snapshot.queryParamMap.get('name');

    this.pageTitle += `: ${this.id}`;

    this.loadFromFile(this.id);
    this.loadImageFromFile(this.id);
    this.AnesthesiaTypes = [
      { item_id: 1, item_text: 'General' },
      { item_id: 2, item_text: 'Regional' },
      { item_id: 3, item_text: 'Mac' }
    ];
    this.BillableItems = [
      { item_id: 1, item_text: 'A - line' },
      { item_id: 2, item_text: 'A - line + US' },
      { item_id: 3, item_text: 'CVP' },
      { item_id: 4, item_text: 'CVP + US' },
      { item_id: 5, item_text: 'PA' },
      { item_id: 3, item_text: 'Postop Blk' },
      { item_id: 4, item_text: 'Postop Blk + US' },
      { item_id: 5, item_text: 'TEE' }
    ];
    this.BillableConsiderations = [
      { item_id: 1, item_text: 'Field avoidance' },
      { item_id: 2, item_text: 'Controlled hypotension' },
      { item_id: 3, item_text: 'Total body hypothermia' },
    ];
    this.Inconsistencies = [
      { item_id: 1, item_text: 'Missing Anes Start' },
      { item_id: 2, item_text: 'Missing Anes Stop' },
      { item_id: 3, item_text: 'Missing MRN' },
      { item_id: 4, item_text: 'Missing Patient Name' },
      { item_id: 5, item_text: 'Missing Date of Procedure' },
      { item_id: 6, item_text: 'Missing Age' },
      { item_id: 7, item_text: 'Missing Gendere' },
      { item_id: 8, item_text: 'Missing ASA' },
      { item_id: 9, item_text: 'Missing Anes Type' },
      { item_id: 10, item_text: 'Missing Provider names' },
      { item_id: 11, item_text: 'Missing Provider Times' },
      { item_id: 12, item_text: 'Missing Invasive Procedure notes' },
      { item_id: 13, item_text: 'Missing attestations' },
    ];
    this.selectedItems = [];
    this.dropdownSettings = {
      singleSelection: false,
      idField: 'item_id',
      textField: 'item_text',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
    this.dropdownSettings1 = {
      singleSelection: false,
      idField: 'item_id',
      textField: 'item_text',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 1,
      allowSearchFilter: true
    };
    this.dropdownSettings2 = {
      singleSelection: false,
      idField: 'item_id',
      textField: 'item_text',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 1,
      allowSearchFilter: true
    };
  }
  onItemSelect2(item: any) {
    console.log(item);
  }
  onSelectAll2(items: any) {
    console.log(items);
  }
  onItemSelect1(item: any) {
    console.log(item);
  }
  onSelectAll1(items: any) {
    console.log(items);
  }
  onItemSelect(item: any) {
    console.log('onItemSelect', item);
  }
  onSelectAll(items: any) {
    console.log('onSelectAll', items);
  }
  toogleShowFilter() {
    this.ShowFilter = !this.ShowFilter;
    this.dropdownSettings = Object.assign({}, this.dropdownSettings, { allowSearchFilter: this.ShowFilter });
  }

  handleLimitSelection() {
    if (this.limitSelection) {
      this.dropdownSettings = Object.assign({}, this.dropdownSettings, { limitSelection: 2 });
    } else {
      this.dropdownSettings = Object.assign({}, this.dropdownSettings, { limitSelection: null });
    }
  }
  changeTitle(index) {
    this.currentImageTitle = this.imageTitles[index];
    console.log('FileName', index, this.currentImageTitle);
  }
  loadFromFile(id) {
    this.http.get(environment.api_url + '/patient/' + id).subscribe(
      result => {
        this.patientdetails = result.json() as PatientDetails;
        this.caseId = this.patientdetails.CaseId;
        this.date = this.patientdetails.AnesthesiaStart;
        this.Procedure = this.patientdetails.Procedure;
        this.OR = this.patientdetails.Location;
        this.DocumentationInconsistencies = this.patientdetails.DocumentationInconsistencies;
        // this.emailAlias = this.patientdetails.Anesthesiologist.concat(this.patientdetails.CRNA);

      },
      error => console.error(error)
    );
  }

  loadImageFromFile(id) {
    this.http.get(environment.api_url + '/patient/image/' + id).subscribe(
      result => {
        //console.log('calling');
        this.PatientImageDetails = result.json() as IPatientImageDetails;
        this.images = this.PatientImageDetails.images;
        this.imageTitles = this.PatientImageDetails.titles;
        // this.images.push('https://storageaccountjiten9941.blob.core.windows.net/revmax-processed/Test-20200618010151-CASE1/20200618010151-CASE1-PREOP-1.png?sv=2018-03-28&sr=b&sig=jsP6Q96z1ThxNTxJJBwz5IK%2BsNZ97fpb7ZcVXU44mRw%3D&se=2023-04-15T03%3A48%3A27Z&sp=rwl')
        console.log(this.images);
      },
      error => console.error(error)
    );
  }
  SaveFormData(savedFormData: NgForm) {
    console.log('Form Data', savedFormData.value);
     this.http.post(environment.api_url + '/patient/UpdatePatientInfo', savedFormData.value).subscribe(
    //this.http.post('http://localhost:52221/patient/UpdatePatientInfo', savedFormData.value).subscribe(
      result => {
        //console.log('calling');
        console.log(result.json());
        // console.log(this.images[0]);
      },
      error => console.error(error)
    );
  }
}



