import { Component, Input, ViewChild, Output, EventEmitter, OnInit, Inject, ViewEncapsulation, Injector } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { MatDialog, MatDialogConfig, MAT_DIALOG_DATA, MatDialogRef, MatSelect, MatFormFieldModule } from '@angular/material';
import { FormControl, Validators } from '@angular/forms';
import { Http } from '@angular/http';
import { IProviderDetails } from '../../../view-models/patient.model';
import { environment } from '../../../../environments/environment';
import { filter } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpHeaders, HttpClient } from '@angular/common/http';

export interface DialogData {
  
  aliases: string[];
  subject: string;
  body: string;
  caseOrMRN: string;
  date: string;
  caseId: string;
  OR: string;
  Procedure: string;
  DocumentationInconsistencies: string;
  hide: boolean;
}

@Component({
  selector: 'app-header',
  templateUrl: './app-header.component.html',
  styleUrls: ['./app-header.component.scss']
})

export class AppHeaderComponent implements OnInit {
  
  public now: string;
  startdate: string;
  enddate: string;
  mrn: string;
  name: string;
  show: string;
  filter: string;
  encounter: string;
  id: string;
  @Input() caseId: string;
  @Input() date: string;
  @Input() OR: string;
  @Input() Procedure: string;
  @Input() DocumentationInconsistencies: string;
  @Input() EncryptedMRN: string;
  aliases: string[] = ['veenasravan@hotmail.com', 'prakashm@perimatics.com', 'bala@perimatics.com'];
  subject: string;
  body: string;
  caseOrMRN: string;
 
  EmailAlias: string[];

  constructor(private route: ActivatedRoute,
    private router: Router,
    private dialog: MatDialog,
    private http: Http) { }

  ngOnInit() {
    this. id = this.route.snapshot.paramMap.get('id');
    this.startdate = this.route.snapshot.queryParamMap.get('startdate');
    this.enddate = this.route.snapshot.queryParamMap.get('enddate');
    this.mrn = this.route.snapshot.queryParamMap.get('mrn');
    this.encounter = this.route.snapshot.queryParamMap.get('encounter');
    this.name = this.route.snapshot.queryParamMap.get('name');
    this.show = this.route.snapshot.queryParamMap.get('show');
    this.filter = this.route.snapshot.queryParamMap.get('filter');
    
    
  }

  loadFromFile(id) {
    
    this.http.get(environment.api_url + '/patient/providerEmail/' + id).subscribe(
      result => {
        this.EmailAlias = result.json()
        console.log(this.EmailAlias);
      },
      error => console.error(error)
    );
  }
  onBack(): void {
    this.router.navigate(['/patient'], {
      queryParams: {
        startdate: moment(this.startdate).format('YYYY-MM-DD'),
        enddate: moment(this.enddate).format('YYYY-MM-DD'),
        name: this.name,
        mrn: this.mrn,
        encounter: this.encounter,
        show: this.show,
        filter: this.filter
      }
    });
  }
  sendEmail(): Observable<any> {
    this.loadFromFile(this.id);
    console.log(this.EmailAlias);
    const dialogConfig = new MatDialogConfig();
    dialogConfig.direction = "ltr";
    dialogConfig.autoFocus = true;
    dialogConfig.width = "60%";
    dialogConfig.height = "60%";

    if (this.caseId == null && this.DocumentationInconsistencies != "") {
      dialogConfig.data = {
        aliases: this.aliases,
        subject: 'Documentation/Billing inconsistencies for the MRN ' + this.EncryptedMRN,
        body: 'Hello, \n\n Please address the following documentation / Billing inconsistencies.',
        caseOrMRN: 'MRN',
        date: moment(this.date).format('YYYY-MM-DD'),
        caseId: this.EncryptedMRN,
        OR: this.OR,
        Procedure: this.Procedure,
        DocumentationInconsistencies: this.DocumentationInconsistencies,
        hide: false
      };
    }
    else if (this.DocumentationInconsistencies !="") {
      dialogConfig.data = {
        aliases: this.aliases,
        subject: 'Documentation/Billing inconsistencies for the Case ID ' + this.caseId,
        body: 'Hello, \n\n Please address the following documentation / Billing inconsistencies.',
        caseOrMRN: 'Case ID',
        date: moment(this.date).format('YYYY-MM-DD'),
        caseId: this.caseId,
        OR: this.OR,
        Procedure: this.Procedure,
        DocumentationInconsistencies: this.DocumentationInconsistencies,
        hide: false
      };
    }

    else {
      dialogConfig.data = {
        aliases: this.aliases,
        subject: 'Billing details for the Case ID ' + this.caseId,
        body: '',
        caseId: this.caseId,
        hide: true
      };
    }


    const dialogRef = this.dialog.open(EmailPopupComponent, dialogConfig);
    dialogRef.afterClosed()
      .pipe(filter(result => result))
      .subscribe(result => {
        console.log("output data:"+ result);
      });

    //const dialogSubmitSubscription =
    //  dialogRef.componentInstance.submitClicked.subscribe(result => {
    //    dialogSubmitSubscription.unsubscribe();
    //  });
    
       return dialogRef.afterClosed();
  }
}

@Component({
  selector: 'app-email-popup',
  templateUrl: '../email-popup/email-popup.component.html',
  styleUrls: ['../email-popup/email-popup.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class EmailPopupComponent implements OnInit  {

  aliases: string[];
  subject: string;
  body: string;
  date: string;
  caseId: string;
  OR: string;
  Procedure: string;
  DocumentationInconsistencies: string;
  hide: boolean;
  caseOrMRN: string;
  SelectedAliases = new FormControl();

  @Output() submitClicked = new EventEmitter<any>();

  @ViewChild(MatSelect) select: MatSelect;

  constructor(
    private route: ActivatedRoute,
    public dialogRef: MatDialogRef<EmailPopupComponent>,
    private http: HttpClient,
    injector: Injector,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {
    
    this.aliases = data.aliases;
    this.subject = this.data.subject
    this.body = data.body;
    this.caseOrMRN = data.caseOrMRN;
    this.date = data.date;
    this.caseId = data.caseId;
    this.OR = data.OR;
    this.Procedure = data.Procedure;
    this.DocumentationInconsistencies = data.DocumentationInconsistencies;
    this.hide = data.hide;
   
  }
  
  ngOnInit() {
    const id = this.route.snapshot.paramMap.get('id');
  }

  send() {
    try {
      var emailObj = {
        toname: this.SelectedAliases.value,
        toemail: this.SelectedAliases.value,
        subject: this.subject,
        message: '<div style="font-family:Segoe UI; font-size:12pt;" > ' + this.body.replace("\n\n","<br/>") + '</div>' + '<br/ >' +
          '<table class=MsoTableGrid border=1 cellspacing=0 cellpadding=0 style="border-collapse:collapse;border:none;mso-border-alt:solid windowtext .5pt; mso- yfti - tbllook:1184; mso - padding - alt: 0cm 5.4pt 0cm 5.4pt">' +

          '<tr style="font-family:Segoe UI; font-size:12pt; height:25px; border: 1px grey; "' + '>' +
          '<td bgcolor= "lightblue" style="font-weight:bold; text-align:center; padding: 4px 7px 4px 7px">Date</td>' +
          '<td bgcolor= "lightblue" style="font-weight:bold; text-align:center; padding: 4px 7px 4px 7px">' + this.caseOrMRN + '</td>' +
          '<td bgcolor= "lightblue" style="font-weight:bold; text-align:center; padding: 4px 7px 4px 7px">OR</td>' +
          '<td bgcolor= "lightblue" style="font-weight:bold; text-align:center; padding: 4px 7px 4px 7px">Procedure</td>' +
          '<td bgcolor= "lightblue" style="font-weight:bold; text-align:center; padding: 4px 7px 4px 7px">Billing/Documentation Inconsistencies</td>' +
          '</tr>' +


          '<tr style="font-family:Segoe UI; font-size:12pt; height:25px;">' +
          '<td style="padding: 4px 7px 4px 7px">' + this.date + ' </td>' +
          '<td style="padding: 4px 7px 4px 7px">' + this.caseId + '</td>' +
          '<td style="padding: 4px 7px 4px 7px">' + this.OR + '</td>' +
          '<td style="padding: 4px 7px 4px 7px">' + this.Procedure + '</td>' +
          '<td style="padding: 4px 7px 4px 7px">' + this.DocumentationInconsistencies + '</td>' +
          '</tr>' +

          '</table>' + '<br/>' + '<span style="font-family:Segoe UI; font-size:12pt;" > Thanks! </span>'

      };
      
      var emailObjNoIssue = {
        toname: this.SelectedAliases.value,
        toemail: this.SelectedAliases.value,
        subject: this.subject,
        message: '<div style="font-family:Segoe UI; font-size:12pt;" >' + this.body + '</div>' + '<br/>  <span style="font-family:Segoe UI; font-size:12pt;" > Thanks! </span>'
      };

      let headers = new HttpHeaders();

      headers = headers.set('Accept', 'application/json');

      if (emailObj || emailObjNoIssue) {
        headers = headers.set('Content-Type', 'application/json');
      }
      console.log(emailObj);
      
      if (this.DocumentationInconsistencies) {
        this.http.post(environment.api_url + '/email/SendEmail/', emailObj, {
          headers
        }).subscribe(result => {
          console.log("Email sent!");
          this.dialogRef.close(emailObj);
        });
      }
      else {
        this.http.post(environment.api_url + '/email/SendEmail/', emailObjNoIssue, {
          headers
        }).subscribe(result => {
          console.log("Email sent NoIssue!");
          this.dialogRef.close(emailObjNoIssue);
        });
      }
    }
    catch(Error) {
      alert(Error.message);
    }
  }
    
  close(): void {
    this.dialogRef.close();
  }

}





