import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { MaterialModule } from './shared/material.module';
import { HttpClientModule } from '@angular/common/http';
import { HttpModule } from '@angular/http';
import { AppRoutingModule, routingComponents } from './app.routing';

import { AppComponent } from './app.component';
import { PatientDataservice } from './services/patient-dataservice';

import { SignInComponent } from './users/sign-in/sign-in.component';
import { AuthGuard } from './services/auth-guard.service';
import { UserService } from './services/user.service';
import { UserApi } from './users/user-api';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppHeaderComponent, EmailPopupComponent } from './ReMaxBilling/component/app-header/app-header.component';
import { MomentModule } from 'angular2-moment';
import { MatButtonModule } from '@angular/material/button';
import { ImageViewerModule } from 'ng2-image-viewer';
import { AppFooterComponent } from './ReMaxBilling/component/app-footer/app-footer.component';
import { MatDialogsHelperModule } from './ReMaxBilling/patient/mat-dialogs-helper/mat-dialogs-helper.module';
import { MatSortModule } from '@angular/material'
import { MatDialogModule } from '@angular/material/dialog';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';

@NgModule({
  declarations: [
    AppComponent,
    routingComponents,
    SignInComponent,
    AppHeaderComponent,
    AppFooterComponent,
    EmailPopupComponent
  ],
  providers: [
    PatientDataservice,
    UserService,
    { provide: UserApi, useExisting: UserService },
    AuthGuard
  ],
  imports: [
    CommonModule,
    HttpClientModule,
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    MaterialModule,
    HttpModule,
    FormsModule,
    MomentModule,
    MatButtonModule,
    ImageViewerModule,
    ReactiveFormsModule,
    MatDialogsHelperModule,
    MatSortModule,
    MatDialogModule,
    NgMultiSelectDropDownModule.forRoot()

  ],
  bootstrap: [AppComponent],
  entryComponents: [AppHeaderComponent, EmailPopupComponent]
})
export class AppModule {}



