import { Injectable } from '@angular/core';
import { Patient, PatientInfo } from '../view-models/patient.model';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { environment } from '../../environments/environment';
@Injectable()
export class PatientDataservice {
  patientSc: Observable<any>;
  constructor(private http: HttpClient)  {}

  getPatientsData() {
    return this.http
      .get('assets/Patient.json')
      .map(response => response.toString());
  }
  SavePatientInfo(procId, status): Observable<any> 
  {       const headers = new HttpHeaders({'Authorization': 'Bearer ' + sessionStorage.getItem('token').toString()});
         return this.http.post(environment.api_url +           '/Patient/UpdatePatientInfo', null, { headers });   }
}



