import { Injectable } from '@angular/core';
import {MatDialog, MatDialogRef} from '@angular/material';
import { InfoDialogComponent } from './info-dialog/info-dialog.component';
import { map } from 'rxjs/operators/map';

@Injectable()
export class MatDialogsHelperService {
  public defaultWidth = '80vw';
  public data;

  constructor(private dialogs: MatDialog) {}

  public confirm(message) {
    let dialogRef: MatDialogRef<InfoDialogComponent>;

    dialogRef = this.dialogs.open(InfoDialogComponent, {
      panelClass: 'my-full-screen-dialog',
    });
    dialogRef.disableClose = true;

    if (message === true) {
      this.data = 'Unmark';
    } else {
      this.data = 'Mark';
    }
    dialogRef.componentInstance.name = message;
    dialogRef.componentInstance.data = this.data;

    return dialogRef.afterClosed().pipe(map((res) => !!res
    ));
  }
}



